export default {
	data() {
		return {
			invitationCode: "",
			owner: "",
			storeName: "",
			storeAddress: "",
			manger: "",
			managerPhone: "",
			imgList: [],
		}
	},
	methods: {
		setInvitationCode(e) {this.invitationCode = e.detail.value},
		setStoreName(e) {this.storeName = e.detail.value},
		setStoreAddress(e) {this.storeAddress = e.detail.value},
		getLocation(e) {
			var that = this;
			uni.chooseLocation({
			  success: function (res) {
				if (res.errMsg == "chooseLocation:ok"){
					//console.log(res)
					that.storeAddress = res.address
					that.$forceUpdate()
				}
			  }
			})
		},
		setManager(e) {this.manger = e.detail.value},
		setManagerPhone(e) {this.managerPhone = e.detail.value},
		ChooseImage(e) {
			uni.chooseImage({
				count: 4, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '你好',
				content: '确定要删除图片吗？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		createCommit(e){
			uni.request({
				method: "POST",
				header: {},
				url: "",
				data: {},
				success(){},
				fail(){},
				complete(){},
			})
		}
	}
}
